import styled from "styled-components";

export const Layout = styled.div`
  max-width: 980px;
  padding: 0px 15px;
  margin: 35px auto;
  position: relative;
`;

export const ArticleTitle = styled.h1`
  font-family: "Galano";
  font-size: 48px;
`;

export const ArticleSubTitle = styled.h2`
  color: #aaaaaa;
  font-size: 20px;
  display: flex;
  align-items: center;
`;

export const Author = styled.div`
  margin-top: 10px;
  .author-image {
    width: 50px;
    height: 50px;
    background: url("/static/img/me-img.jpg") no-repeat center;
    border-radius: 50%;
    background-size: cover;
    float: left;
  }
  .author-name-and-labels {
    float: left;
    margin-left: 15px;
    .author-name {
      margin: 0;
      font-size: 17px;
      margin-bottom: 5px;
    }
  }
  &::after {
    content: "";
    display: block;
    clear: both;
  }
`;

export const MainPhoto = styled.div`
  width: 100%;
  height: 400px;
  background: ${props => `url("/static/img/work/main/${
    props.img
  }.png") no-repeat
    center;`};
  /* background-attachment: fixed; */
  background-size: cover;
`;

export const DateAndLabels = styled.div`
  display: flex;
  align-items: center;
  .date {
    margin: 0;
    color: #aaaaaa;
    padding-right: 10px;
  }
  .label-tag {
    margin-top: 0 !important;
  }
`;

export const LabelsDiv = styled.div`
  display: flex;
  padding-bottom: 35px;
  margin-bottom: 35px;
  border-bottom: 1px solid #e9e8e7;
`;
